import { useDispatch } from "react-redux";

function ExampleTasksButton() {
  const dispatch = useDispatch();

  return (
    <button
      type="button"
      onClick={() => dispatch({ type: "tasks/fetchExampleTasks" })}
      style={{
        background: "none",
        border: "none",
        color: "#008080",
        cursor: "pointer",
        fontSize: "16px",
      }}
    >
      Pobierz przykadowe zadania
    </button>
  );
}

export default ExampleTasksButton;
