import { takeEvery, put, select } from "redux-saga/effects";
import { loadTasks, addManyTasks } from "./tasksSlice";

const KEY = "tasks";

function* saveTasks() {
  const tasks = yield select(state => state.tasks.tasks);
  localStorage.setItem(KEY, JSON.stringify(tasks));
}

function* loadTasksFromStorage() {
  const tasks = JSON.parse(localStorage.getItem(KEY)) || [];
  yield put(loadTasks(tasks));
}

function* fetchExampleTasks() {
  yield put(
    addManyTasks([
      { id: Date.now(), text: "Przykadowe zadanie 1", done: false },
      { id: Date.now() + 1, text: "Przykadowe zadanie 2", done: false },
    ])
  );
}

export function* tasksSaga() {
  yield loadTasksFromStorage();

  yield takeEvery(
    [
      "tasks/addTask",
      "tasks/addManyTasks",
      "tasks/deleteTask",
      "tasks/toggleTask",
      "tasks/finishAll",
    ],
    saveTasks
  );

  yield takeEvery("tasks/fetchExampleTasks", fetchExampleTasks);
}
