import { NavLink } from "react-router-dom";

function Navigation() {
  return (
    <nav className="navigation">
      <ul className="navigation__list">
        <li>
          <NavLink to="/" className="navigation__link">
            Zadania
          </NavLink>
        </li>
        <li>
          <NavLink to="/about" className="navigation__link">
            O autorze
          </NavLink>
        </li>
      </ul>
    </nav>
  );
}

export default Navigation;
