import {
  Item,
  CheckButton,
  TextLink,
  DeleteButton,
} from "./TaskItem.styles";

function TaskItem({ task, onToggle, onDelete }) {
  return (
    <Item>
      <CheckButton onClick={() => onToggle(task.id)}>
        {task.done && ""}
      </CheckButton>

      <TextLink to={`/tasks/${task.id}`} $done={task.done}>
        {task.text}
      </TextLink>

      <DeleteButton onClick={() => onDelete(task.id)}>
        
      </DeleteButton>
    </Item>
  );
}

export default TaskItem;
