import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import TaskForm from "../components/TaskForm";
import TaskList from "../components/TaskList";
import TaskControl from "../components/TaskControl";
import TaskFilter from "../components/TaskFilter";
import ExampleTasksButton from "../components/ExampleTasksButton";
import { finishAll, selectFilteredTasks } from "../store/tasksSlice";

function TasksPage() {
  const [showCompleted, setShowCompleted] = useState(true);
  const dispatch = useDispatch();

  const filteredTasks = useSelector(selectFilteredTasks);
  const allTasks = useSelector(state => state.tasks.tasks);

  const visibleTasks = showCompleted
    ? filteredTasks
    : filteredTasks.filter(t => !t.done);

  return (
    <>
      <h1 className="title">Lista zada</h1>

      <section className="card">
        <div className="card__header">
          <h2>Dodaj nowe zadanie</h2>
          <ExampleTasksButton />
        </div>
        <TaskForm />
      </section>

      <TaskFilter />

      <section className="card">
        <div className="card__header">
          <h2>Lista zada</h2>
          <TaskControl
            showCompleted={showCompleted}
            setShowCompleted={setShowCompleted}
            finishAll={() => dispatch(finishAll())}
            tasks={allTasks}
          />
        </div>

        <TaskList tasks={visibleTasks} />
      </section>
    </>
  );
}

export default TasksPage;
